<template>
  <div id="home_page">
    <!-- 表头开始 -->
    <head-top>
      <svg slot="left-icon" class="icon" aria-hidden="true">
        <use xlink:href="#icon-maikefeng"></use>
      </svg>

      <input slot="search" type="text" class="search" :placeholder="searchTip">
      <router-link to='player' slot="right-icon">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-yinle"></use>
        </svg>
      </router-link>
    </head-top>
    <!-- 表头结束 -->
    <!-- 导航开始 -->
    <nav class='home_nav zx10'>
      <div class='nav_title'>
        <p :class="navNum == 1 ? 'on' : ''" @click='navNum=1'>音乐</p>
      </div>
      <div class='nav_title'>
        <p :class="navNum == 2 ? 'on' : ''" @click='navNum=2'>视频</p>
      </div>
      <div class='nav_title'>
        <p :class="navNum == 3 ? 'on' : ''" @click='navNum=3'>电台</p>
      </div>
    </nav>
    <!-- 导航结束 -->
    <!-- 轮播图开始 -->
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div v-for="(item,index) in swiperUrl" :key="index" class="swiper-slide">
          <img :src="item.pic" alt="" class="swiper_img">
        </div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
    <!-- 轮播图结束 -->
    <!-- 链接开始 -->
    <section class='clear'>
      <router-link class='link-fm left' tag="li" to='fm'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-fm"></use>
        </svg>
        <span>私人FM</span>
      </router-link>
      <router-link class='link-everyday left' tag="li" to='everyday'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-rili"></use>
        </svg>
        <span>每日推荐</span>
      </router-link>
      <router-link class='link-songlist left' tag="li" to='songlist'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-gedan"></use>
        </svg>
        <span>歌单</span>
      </router-link>
      <router-link class='link-rank left' tag="li" to='rank'>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-ttpodicon"></use>
        </svg>
        <span>排行榜</span>
      </router-link>
    </section>
    <!-- 链接结束 -->
    <!-- 推荐歌单开始 -->
    <section class="recommond">
      <router-link to='songlist' class="recommend_title">
        <span class="">推荐歌单</span>
        <svg class="icon" aria-hidden="true"style="color: red;">
          <use xlink:href="#icon-yinyue"></use>
        </svg>
      </router-link>
      <ul class="recommondlist">
        <router-link v-for="(item,index) in recommend" tag="li" to='songlist' :key="index">
          <div class="list_img">
            <img 
            v-lazy="item.picUrl" 
            alt="预览图">
            <!-- <loading class="loading" v-if=""></loading> -->
            <aside>
              <span class="list_count">
                {{item.playCount}}万
              </span>
            </aside>
          </div>
          <p class="list_title ellis">
            {{item.name}}
          </p>
        </router-link>
      </ul>
    </section>
    <!-- 推荐歌单结束 -->
    <!-- 独家放送开始 -->
    <!-- 独家放送结束 -->
  </div>
</template>.

<script type="text/javascript">
  import headTop from '../../components/header/headTop';
  // import loading from '../../components/common/loading';
  import { getBannerResource,getPersonalizedResource } from '../../service/getData';
  import Swiper from 'swiper';
  import '../../style/swiper.min.css';
  export default {
    components: {
      headTop,
      // loading
    },
    props: [],
    mixins: [],
    data() {
      return {
        searchTip: '搜索音乐、歌词、电台',       //搜索框提示
        navNum: '1',                           //导航选中项序号
        swiperUrl: [],                         //轮播图地址
        recommend: [],                         //推荐歌单
      }
    },
    created() {
      //数据预处理
    },
    mounted() {
      //请求数据
      this.initData();
    },
    computed: {

    },
    methods: {
      async initData() {
        getBannerResource().then(res => {
          this.swiperUrl = [...res.banners];
        }).then(() => {
          new Swiper('.swiper-container', {
            autoplay: true,
            loop: true,
            pagination: {
              el: '.swiper-pagination',
            }
          })
        });
        getPersonalizedResource().then(res => {
          for(let item of res.result){
            item.playCount = Math.round(item.playCount/10000);
          }
          this.recommend = [...res.result].slice(0,6);
          console.log(this.recommend);
        });
      }
    },
    watch: {

    }
  }
</script>

<style lang="scss" scoped>
  @import '../../style/mixin';
  .head_top {
    .search {
      @include wh(5.5rem, 0.6rem);
      @include borderRadius(0.3rem);
      font-size: $f30;
      text-align: center;
    }
  }

  .home_nav {
    @include wh(100%, 0.7rem);
    @include font($f30, 0.7rem);
    opacity: 0.95;
    text-align: center;
    display: flex;
    position: fixed;
    left: 0;
    top: 1rem;
    background-color: #fff;
    .nav_title {
      flex: 1;
    }
    .on {
      width: 0.8rem;
      margin: 0 auto;
      border-bottom: 0.03rem solid $red;
    }
  }

  .swiper-container {
    margin-top: 1.73rem;
    @include wh(100%, 3rem);
    .swiper_img {
      @include wh(100%, 100%);
    }
  }

  .link-fm,
  .link-everyday,
  .link-songlist,
  .link-rank {
    @include wh(25%, 1.8rem);
    padding: 0.2rem 0.3rem;
    border-bottom: 0.01rem solid $gray;
    text-align: center;
    .icon{
      color: $red;
      @include wh(75%,75%);
    }
    span{
      font-size: $f25;
    }
  }
  .recommend_title{
    @include wh(100%,1rem);
    padding: 0.2rem 0;
    span{
      padding: 0 0.2rem;
      border-left: 0.03rem solid $red;
      font-size: $f35;
    }
  }
  .recommondlist{
    padding-bottom: 0.5rem;
    display: flex;
    flex-wrap: wrap;
    li{
      @include wh(33.3%,3.5rem);
      padding-right: 0.1rem;
      &:nth-child(3n){
        padding-right: 0;
      }
      .list_img{
        position: relative;
        /* .loading{
          @include center;
        } */
        img{
          @include wh(100%,2.5rem);
          transform-origin: 0;
          transform: rotatey(-90deg);
          transition: all .4s  ease-out;
          &[lazy=loaded] {
            transform: rotatey(0deg);
          }
        }
        span{
          position: absolute;
          top: 0.1rem;
          right: 0.2rem;
          color: #fff;
        }
      }
      .list_title{
        padding-top: 0.1rem;
        font-size: $f25;
        text-align: center;
      }
    }
  }
</style>



